'use client';

import { useState, useMemo } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import type { Product, PriceType } from '@/lib/types';
import type { Locale } from '@/i18n-config';
import { ArrowUpDown, Filter, Eye, ShoppingCart, GitCompare, Download, ChevronDown, ChevronUp } from 'lucide-react';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';
import { getPriceLabel, getDisplayPrice, getAllPrices } from '@/lib/utils/priceTypes';

interface PriceTableProps {
  products: Product[];
  lang: Locale;
  dictionary?: {
    common?: {
      view_details: string;
    };
    product?: {
      add_to_cart: string;
      add_to_compare: string;
      in_stock: string;
      out_of_stock: string;
      contact_for_price: string;
    };
    price_table?: {
      title: string;
      subtitle: string;
      search_placeholder: string;
      filter_by: string;
      all_categories: string;
      sort_by: string;
      product_name: string;
      price: string;
      dimensions: string;
      grade: string;
      stock: string;
      actions: string;
      no_results: string;
      sort_name_asc: string;
      sort_name_desc: string;
      sort_price_asc: string;
      sort_price_desc: string;
      export_pdf: string;
      price_per_piece?: string;
      price_per_meter?: string;
      price_per_sqm?: string;
      price_per_cubic_foot?: string;
      price_per_cubic_meter?: string;
      price_per_board_foot?: string;
      price_types?: string;
      view_all_prices?: string;
    };
  };
}

type SortOption = 'name-asc' | 'name-desc' | 'price-asc' | 'price-desc';
type CategoryFilter = 'all' | 'structural' | 'plywood' | 'furniture' | 'decking';

export function PriceTable({ products, lang, dictionary }: PriceTableProps) {
  const [searchTerm, setSearchTerm] = useState('');
  const [categoryFilter, setCategoryFilter] = useState<CategoryFilter>('all');
  const [sortBy, setSortBy] = useState<SortOption>('name-asc');
  const [expandedPriceRows, setExpandedPriceRows] = useState<Set<string>>(new Set());

  const dict = dictionary?.price_table || {
    title: lang === 'th' ? 'ตารางราคาสินค้า' : 'Product Price List',
    subtitle: lang === 'th'
      ? 'เปรียบเทียบราคาและสเปกสินค้าได้ที่นี่'
      : 'Compare prices and specifications at a glance',
    search_placeholder: lang === 'th' ? 'ค้นหาสินค้า...' : 'Search products...',
    filter_by: lang === 'th' ? 'กรองตามหมวดหมู่:' : 'Filter by:',
    all_categories: lang === 'th' ? 'ทั้งหมด' : 'All Categories',
    sort_by: lang === 'th' ? 'เรียงตาม:' : 'Sort by:',
    product_name: lang === 'th' ? 'ชื่อสินค้า' : 'Product',
    price: lang === 'th' ? 'ราคา' : 'Price',
    dimensions: lang === 'th' ? 'ขนาด' : 'Dimensions',
    grade: lang === 'th' ? 'เกรด' : 'Grade',
    stock: lang === 'th' ? 'สถานะ' : 'Stock',
    actions: lang === 'th' ? 'ดำเนินการ' : 'Actions',
    no_results: lang === 'th' ? 'ไม่พบสินค้า' : 'No products found',
    sort_name_asc: lang === 'th' ? 'ชื่อ A-Z' : 'Name A-Z',
    sort_name_desc: lang === 'th' ? 'ชื่อ Z-A' : 'Name Z-A',
    sort_price_asc: lang === 'th' ? 'ราคา น้อย-มาก' : 'Price Low-High',
    sort_price_desc: lang === 'th' ? 'ราคา มาก-น้อย' : 'Price High-Low',
    export_pdf: lang === 'th' ? 'ส่งออก PDF' : 'Export PDF',
  };

  // Export to PDF function
  const exportToPDF = () => {
    const doc = new jsPDF();

    // Add title
    doc.setFontSize(18);
    doc.text(dict.title, 14, 22);

    // Add subtitle
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.text(dict.subtitle, 14, 30);
    doc.text(`Generated: ${new Date().toLocaleDateString()}`, 14, 36);

    // Prepare table data
    const tableData = filteredAndSortedProducts.map(product => {
      const price = product.price
        ? `${product.price}${lang === 'th' ? ' บาท' : ' THB'}`
        : (lang === 'th' ? 'ติดต่อ' : 'Contact');
      const dimensions = getDimensions(product);
      const grade = getGrade(product);
      const stockStatus = getStockStatus(product);

      return [
        product.name,
        dimensions,
        grade,
        price,
        stockStatus.text
      ];
    });

    // Add table
    autoTable(doc, {
      startY: 44,
      head: [[
        dict.product_name,
        dict.dimensions,
        dict.grade,
        dict.price,
        dict.stock
      ]],
      body: tableData,
      theme: 'grid',
      styles: {
        fontSize: 10,
        cellPadding: 3,
      },
      headStyles: {
        fillColor: [30, 58, 138],
        textColor: 255,
        fontStyle: 'bold',
      },
      alternateRowStyles: {
        fillColor: [245, 247, 250],
      },
      columnStyles: {
        0: { cellWidth: 60 }, // Product name
        1: { cellWidth: 40 }, // Dimensions
        2: { cellWidth: 25 }, // Grade
        3: { cellWidth: 30 }, // Price
        4: { cellWidth: 30 }, // Stock
      },
    });

    // Add footer
    const pageCount = doc.internal.pages.length - 1;
    for (let i = 1; i <= pageCount; i++) {
      doc.setPage(i);
      doc.setFontSize(9);
      doc.setTextColor(150);
      doc.text(
        `SAK WoodWorks - ${lang === 'th' ? 'ผู้จำหน่ายไม้คุณภาพสูง' : 'Premium Wood Products Supplier'}`,
        14,
        doc.internal.pageSize.height - 10
      );
      doc.text(
        `Page ${i} of ${pageCount}`,
        doc.internal.pageSize.width - 30,
        doc.internal.pageSize.height - 10,
        { align: 'right' }
      );
    }

    // Save PDF
    const fileName = `sakwood-price-list-${lang}-${new Date().toISOString().split('T')[0]}.pdf`;
    doc.save(fileName);
  };

  // Filter and sort products
  const filteredAndSortedProducts = useMemo(() => {
    let filtered = [...products];

    // Apply search filter
    if (searchTerm) {
      filtered = filtered.filter(product =>
        product.name.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // Apply category filter (based on product categories)
    if (categoryFilter !== 'all') {
      filtered = filtered.filter(product => {
        const categories = product.productCategories?.nodes || [];
        const categorySlugs = categories.map((c: any) => c.slug);
        return categorySlugs.includes(categoryFilter);
      });
    }

    // Apply sorting
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'name-asc':
          return a.name.localeCompare(b.name);
        case 'name-desc':
          return b.name.localeCompare(a.name);
        case 'price-asc':
          const priceA = parseFloat(a.price?.replace(/[^\d.]/g, '') || '0');
          const priceB = parseFloat(b.price?.replace(/[^\d.]/g, '') || '0');
          return priceA - priceB;
        case 'price-desc':
          const priceA2 = parseFloat(a.price?.replace(/[^\d.]/g, '') || '0');
          const priceB2 = parseFloat(b.price?.replace(/[^\d.]/g, '') || '0');
          return priceB2 - priceA2;
        default:
          return 0;
      }
    });

    return filtered;
  }, [products, searchTerm, categoryFilter, sortBy]);

  // Helper function to extract dimensions
  const getDimensions = (product: Product): string => {
    const length = product.productLength || '-';
    const width = product.productWidth || '-';
    const thickness = product.productThickness || '-';

    if (length === '-' && width === '-' && thickness === '-') {
      return lang === 'th' ? 'ไม่ระบุ' : 'N/A';
    }

    const dims = [];
    if (thickness !== '-') dims.push(`${thickness}${lang === 'th' ? 'มม.' : 'mm'}`);
    if (width !== '-') dims.push(`${width}${lang === 'th' ? 'มม.' : 'mm'}`);
    if (length !== '-') dims.push(`${length}${lang === 'th' ? 'ม.' : 'm'}`);

    return dims.join(' × ');
  };

  // Helper function to get grade
  const getGrade = (product: Product): string => {
    return product.productGrade || (lang === 'th' ? 'เกรด A' : 'Grade A');
  };

  // Helper function to get stock status
  const getStockStatus = (product: Product) => {
    const stock = product.stockStatus || 'instock';
    if (stock === 'instock') {
      return {
        text: dict.in_stock || (lang === 'th' ? 'มีสินค้า' : 'In Stock'),
        className: 'bg-green-100 text-green-700'
      };
    }
    return {
      text: dict.out_of_stock || (lang === 'th' ? 'หมด' : 'Out of Stock'),
      className: 'bg-red-100 text-red-700'
    };
  };

  return (
    <div className="w-full">
      {/* Header */}
      <div className="mb-8">
        <h2 className="text-3xl font-bold text-gray-900 mb-2">{dict.title}</h2>
        <p className="text-gray-600">{dict.subtitle}</p>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-xl shadow-md p-6 mb-6 border border-gray-200">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 mb-4">
          <div>
            <h3 className="text-lg font-semibold text-gray-900">
              {lang === 'th' ? 'ตัวเลือกการแสดงผล' : 'Display Options'}
            </h3>
            <p className="text-sm text-gray-500">
              {filteredAndSortedProducts.length} {lang === 'th' ? 'สินค้า' : 'products'}
            </p>
          </div>
          <button
            onClick={exportToPDF}
            disabled={filteredAndSortedProducts.length === 0}
            className="inline-flex items-center gap-2 px-6 py-3 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 transition-colors disabled:bg-gray-300 disabled:cursor-not-allowed shadow-md hover:shadow-lg"
          >
            <Download className="w-5 h-5" />
            {dict.export_pdf}
          </button>
        </div>
        <div className="grid md:grid-cols-3 gap-4">
          {/* Search */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              {dict.search_placeholder}
            </label>
            <input
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
              placeholder={dict.search_placeholder}
            />
          </div>

          {/* Category Filter */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              {dict.filter_by}
            </label>
            <select
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value as CategoryFilter)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
            >
              <option value="all">{dict.all_categories}</option>
              <option value="structural">{lang === 'th' ? 'ไม้โครงสร้าง' : 'Structural'}</option>
              <option value="plywood">{lang === 'th' ? 'เพลย์วูด' : 'Plywood'}</option>
              <option value="furniture">{lang === 'th' ? 'เฟอร์นิเจอร์' : 'Furniture'}</option>
              <option value="decking">{lang === 'th' ? 'ไม้ปูพื้น' : 'Decking'}</option>
            </select>
          </div>

          {/* Sort */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              {dict.sort_by}
            </label>
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as SortOption)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
            >
              <option value="name-asc">{dict.sort_name_asc}</option>
              <option value="name-desc">{dict.sort_name_desc}</option>
              <option value="price-asc">{dict.sort_price_asc}</option>
              <option value="price-desc">{dict.sort_price_desc}</option>
            </select>
          </div>
        </div>
      </div>

      {/* Table */}
      <div className="bg-white rounded-xl shadow-md overflow-hidden border border-gray-200">
        {/* Desktop Table */}
        <div className="hidden md:block overflow-x-auto">
          <table className="w-full">
            <thead className="bg-blue-900 text-white">
              <tr>
                <th className="px-6 py-4 text-left text-sm font-semibold">{dict.product_name}</th>
                <th className="px-6 py-4 text-left text-sm font-semibold">{dict.dimensions}</th>
                <th className="px-6 py-4 text-left text-sm font-semibold">{dict.grade}</th>
                <th className="px-6 py-4 text-left text-sm font-semibold">{dict.price}</th>
                <th className="px-6 py-4 text-left text-sm font-semibold">{dict.stock}</th>
                <th className="px-6 py-4 text-center text-sm font-semibold">{dict.actions}</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {filteredAndSortedProducts.length > 0 ? (
                filteredAndSortedProducts.map((product, index) => {
                  const stockStatus = getStockStatus(product);
                  return (
                    <tr
                      key={product.id}
                      className={`hover:bg-blue-50 transition-colors ${
                        index % 2 === 0 ? 'bg-white' : 'bg-gray-50'
                      }`}
                    >
                      <td className="px-6 py-4">
                        <div className="flex items-center gap-4">
                          {product.image?.sourceUrl && (
                            <div className="relative w-16 h-16 flex-shrink-0">
                              <Image
                                src={product.image.sourceUrl}
                                alt={product.name}
                                fill
                                className="object-cover rounded-lg"
                                unoptimized
                              />
                            </div>
                          )}
                          <div>
                            <Link
                              href={`/${lang}/products/${product.slug}`}
                              className="font-semibold text-gray-900 hover:text-blue-600 transition-colors"
                            >
                              {product.name}
                            </Link>
                            {product.shortDescription && (
                              <p
                                className="text-sm text-gray-500 mt-1 line-clamp-1"
                                dangerouslySetInnerHTML={{ __html: product.shortDescription }}
                              />
                            )}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-700">
                        {getDimensions(product)}
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-700">
                        {getGrade(product)}
                      </td>
                      <td className="px-6 py-4">
                        <span className="text-lg font-bold text-blue-900">
                          {product.price
                            ? `${product.price}${lang === 'th' ? ' บาท' : ' THB'}`
                            : (lang === 'th' ? 'ติดต่อ' : 'Contact')
                          }
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <span
                          className={`inline-flex px-3 py-1 rounded-full text-xs font-semibold ${stockStatus.className}`}
                        >
                          {stockStatus.text}
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center justify-center gap-2">
                          <Link
                            href={`/${lang}/products/${product.slug}`}
                            className="p-2 text-blue-600 hover:bg-blue-100 rounded-lg transition-colors"
                            title={dictionary?.common?.view_details || (lang === 'th' ? 'ดูรายละเอียด' : 'View Details')}
                          >
                            <Eye className="w-5 h-5" />
                          </Link>
                          <button
                            className="p-2 text-green-600 hover:bg-green-100 rounded-lg transition-colors"
                            title={dictionary?.product?.add_to_cart || (lang === 'th' ? 'ใส่ตะกร้า' : 'Add to Cart')}
                          >
                            <ShoppingCart className="w-5 h-5" />
                          </button>
                          <button
                            className="p-2 text-purple-600 hover:bg-purple-100 rounded-lg transition-colors"
                            title={dictionary?.product?.add_to_compare || (lang === 'th' ? 'เปรียบเทียบ' : 'Compare')}
                          >
                            <GitCompare className="w-5 h-5" />
                          </button>
                        </div>
                      </td>
                    </tr>
                  );
                })
              ) : (
                <tr>
                  <td colSpan={6} className="px-6 py-12 text-center">
                    <p className="text-gray-500 text-lg">{dict.no_results}</p>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/* Mobile Cards */}
        <div className="md:hidden px-4 py-6 space-y-4">
          {filteredAndSortedProducts.length > 0 ? (
            filteredAndSortedProducts.map((product) => {
              const stockStatus = getStockStatus(product);
              return (
                <div
                  key={product.id}
                  className="bg-white border border-gray-200 rounded-lg p-4 space-y-3"
                >
                  <div className="flex gap-3">
                    {product.image?.sourceUrl && (
                      <div className="relative w-20 h-20 flex-shrink-0">
                        <Image
                          src={product.image.sourceUrl}
                          alt={product.name}
                          fill
                          className="object-cover rounded-lg"
                          unoptimized
                        />
                      </div>
                    )}
                    <div className="flex-1 min-w-0">
                      <Link
                        href={`/${lang}/products/${product.slug}`}
                        className="font-semibold text-gray-900 hover:text-blue-600 transition-colors"
                      >
                        {product.name}
                      </Link>
                      <p className="text-lg font-bold text-blue-900 mt-1">
                        {product.price
                          ? `${product.price}${lang === 'th' ? ' บาท' : ' THB'}`
                          : (lang === 'th' ? 'ติดต่อ' : 'Contact')
                        }
                      </p>
                    </div>
                  </div>
                  <div className="grid grid-cols-2 gap-2 text-sm">
                    <div>
                      <span className="text-gray-500">{dict.dimensions}:</span>
                      <span className="ml-1 text-gray-900">{getDimensions(product)}</span>
                    </div>
                    <div>
                      <span className="text-gray-500">{dict.grade}:</span>
                      <span className="ml-1 text-gray-900">{getGrade(product)}</span>
                    </div>
                  </div>
                  <div className="flex items-center justify-between pt-2 border-t border-gray-200">
                    <span
                      className={`inline-flex px-3 py-1 rounded-full text-xs font-semibold ${stockStatus.className}`}
                    >
                      {stockStatus.text}
                    </span>
                    <div className="flex gap-2">
                      <Link
                        href={`/${lang}/products/${product.slug}`}
                        className="p-2 text-blue-600 hover:bg-blue-100 rounded-lg transition-colors"
                      >
                        <Eye className="w-5 h-5" />
                      </Link>
                      <button className="p-2 text-green-600 hover:bg-green-100 rounded-lg transition-colors">
                        <ShoppingCart className="w-5 h-5" />
                      </button>
                    </div>
                  </div>
                </div>
              );
            })
          ) : (
            <div className="text-center py-12">
              <p className="text-gray-500 text-lg">{dict.no_results}</p>
            </div>
          )}
        </div>
      </div>

      {/* Summary */}
      {filteredAndSortedProducts.length > 0 && (
        <div className="mt-6 bg-blue-50 rounded-lg p-4 text-center">
          <p className="text-blue-900">
            {lang === 'th'
              ? `แสดง ${filteredAndSortedProducts.length} จาก ${products.length} สินค้า`
              : `Showing ${filteredAndSortedProducts.length} of ${products.length} products`
            }
          </p>
        </div>
      )}
    </div>
  );
}
